import Image from 'next/image';
import { Card as ICard } from '@/models/card.model';
import styled from 'styled-components';
import Text from '@/components/common/Text';
import Title from './Title';

interface Props {
	ref?: React.Ref<HTMLDivElement>;
	type?: 'main' | 'sub';
	className?: string;
	data: ICard;
}

const Card = ({ ref = null, type = 'sub', className, data }: Props) => {
	const { image, header, main, footer } = data;

	return (
		<StyledCard ref={ref} className={className} $type={type}>
			{type === 'main' ? (
				<div className="card-body">
					<div className="card-header">
						<Text size="medium" color="primary" weight="semiBold">
							{header}
						</Text>
						{main.title && (
							<Title className="title" weight="semiBold">
								{main.title}
							</Title>
						)}
					</div>
					<div className="card-main">
						<div className="content">
							{main.description && (
								<Text className="description" size="medium">
									{main.description}
								</Text>
							)}
							{footer && <div className="card-footer">{footer}</div>}
						</div>
						<div className="image-placeholder">
							{image && (
								<Image
									src={image}
									alt={main.title || 'Card Image'}
									width={300}
									height={150}
									style={{
										width: '100%',
										height: '100%',
									}}
									priority
								/>
							)}
						</div>
					</div>
				</div>
			) : (
				<>
					<div className="card-body">
						<div className="image-placeholder">
							{image && (
								<Image
									src={image}
									alt={main.title || 'Card Image'}
									width={300}
									height={150}
									style={{
										width: '100%',
										height: '100%',
									}}
									priority
								/>
							)}
						</div>
						<div className="card-header">
							{header && (
								<Text size="small" color="primary" weight="semiBold">
									{header}
								</Text>
							)}
						</div>
						<div className="card-main">
							<div className="content">
								{main.title && (
									<Text className="title" size="large" weight="semiBold">
										{main.title}
									</Text>
								)}
								{main.description && (
									<Text className="description" size="small">
										{main.description}
									</Text>
								)}
							</div>
						</div>
					</div>
					{footer && <div className="card-footer">{footer}</div>}
				</>
			)}
		</StyledCard>
	);
};

interface StyledProps {
	$type: 'main' | 'sub';
}

const StyledCard = styled.div<StyledProps>`
	width: 100%;
	height: fit-content;

	display: flex;
	flex-direction: column;
	overflow: hidden;
	cursor: pointer;

	.card-body {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		gap: ${({ $type }) => ($type === 'main' ? '1rem' : '0.5rem')};

		&:hover {
			img {
				transform: scale(1.08);
				transition: transform 0.3s ease;
			}
		}

		.image-placeholder {
			width: ${({ $type }) => ($type === 'main' ? '50%' : '100%')};
			aspect-ratio: 16 / 9;
			overflow: hidden;
			border-radius: ${({ theme }) => theme.borderRadius.medium};
			background: ${({ theme }) => theme.color.surface};
			margin-bottom: ${({ $type }) => ($type === 'main' ? '0' : '1rem')};

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.card-header {
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: ${({ $type }) => ($type === 'main' ? 'center' : 'flex-start')};

			.title {
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 1;
				-webkit-box-orient: vertical;
				white-space: normal;
			}
		}

		.card-main {
			width: 100%;
			display: flex;
			flex-direction: ${({ $type }) => ($type === 'main' ? 'row' : 'column')};
			justify-content: center;
			align-items: flex-start;
			margin-bottom: ${({ $type }) => ($type === 'main' ? '0' : '0.5rem')};
			gap: ${({ $type }) => ($type === 'main' ? '2rem' : '0')};

			.content {
				width: ${({ $type }) => ($type === 'main' ? '50%' : '100%')};
				height: 100%;
				display: flex;
				flex-direction: column;
				flex-grow: 1;
				gap: ${({ $type }) => ($type === 'main' ? '1rem' : '0')};

				.title {
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 1;
					-webkit-box-orient: vertical;
					white-space: normal;
				}

				.description {
					overflow: hidden;
					text-overflow: ellipsis;

					display: -webkit-box;
					-webkit-line-clamp: ${({ $type }) => ($type === 'main' ? '8' : '2')};
					-webkit-box-orient: vertical;
					white-space: normal;
				}
			}
		}
	}

	.card-footer {
		width: 100%;
		height: fit-content;

		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-top: auto;
		gap: 0.5rem;
		color: ${({ theme }) => theme.color.subText};

		span {
			white-space: nowrap;
			color: ${({ theme }) => theme.color.subText};
		}

		button {
			width: 100%;
		}

		.rounded-icon {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: center;
			padding: 0;
			padding-right: 0.5rem;
			margin: 0;
			gap: 0.5rem;

			span {
				&:hover {
					color: ${({ theme }) => theme.color.primary};
				}
			}

			button {
				width: ${({ theme }) => theme.fontSize.extraLarge};
				height: ${({ theme }) => theme.fontSize.extraLarge};

				padding: 0;
				margin: 0;
				font-size: ${({ theme }) => theme.fontSize.large};

				color: ${({ theme }) => theme.color.primary};
				border-radius: ${({ theme }) => theme.borderRadius.circle};

				svg {
					font-size: ${({ theme }) => theme.fontSize.medium};
				}

				&:hover {
					background: ${({ theme }) => theme.color.tertiary};
				}
			}
		}

		span {
			font-size: ${({ theme }) => theme.fontSize.small};
			color: ${({ theme }) => theme.color.subText};
		}

		.right {
			flex: 1;
			display: flex;
			flex-direction: row;
			justify-content: flex-end;
			align-items: center;
			gap: 1rem;
		}
	}
`;

export default Card;
