import { useModal } from '@/hooks/useModal';
import { bannerData } from '@/mocks/index';

import { styled } from 'styled-components';
import Title from '@/components/common/Title';
import Button from '@/components/common/Button';
import Modal from '@/components/common/Modal';
import BannerSlider from '@/components/common/slider/BannerSlider';

const HeroSection = () => {
	const { isOpen, modalType, openModal, closeModal } = useModal();

	return (
		<BannerSection>
			<div className="left-section">
				<div className="description">
					<Title size="small">새로운 AI 구독 서비스</Title>
					<Title size="large" weight="semiBold">
						원하는 분야만 골라보는 나만의 뉴스
					</Title>
				</div>
				<Button
					size="large"
					scheme="primary"
					style={{ marginTop: '1rem' }}
					onClick={() => openModal('explore')}
				>
					AI 뉴스레터 체험하기
				</Button>
			</div>
			<BannerSlider banners={bannerData} />
			{isOpen && modalType === 'explore' && (
				<Modal isOpen={isOpen} onClose={closeModal}>
					<Title size="medium">뉴스레터 생성하기</Title>
					<Button>생성하기</Button>
				</Modal>
			)}
		</BannerSection>
	);
};

const BannerSection = styled.section`
	width: 100%;
	height: fit-content;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	gap: 2rem;
	padding: 3rem 0 2rem 0;

	.left-section {
		width: 100%;
		height: 100%;

		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		padding: 2rem 0;
	}

	img {
		border-radius: ${({ theme }) => theme.borderRadius.soft};
		box-shadow: ${({ theme }) => theme.shadow.medium};
	}

	@media ${({ theme }) => theme.mediaQuery.mobile} {
		flex-wrap: wrap;
	}
`;

export default HeroSection;
